.searchContainer {
  display: block;
  position:absolute;
  right: 10px;
  left: auto;

  .search, .close {
    outline: 0;
  }

  .searchBtnActive {
    opacity: 0.5;

    &:hover {
      opacity: 0.5;
      cursor: default;
    }
  }

  .inline-search {
    border: none;
    margin: 0;
    padding: 0;
    background-color: transparent;
    @include font-md();
    font-weight: $weight-light;
    line-height: $line-height-h5;
    font-style: italic;
    width: 85%;
    vertical-align: top;
  }
}