/**
 * Form fields
 */

/* General */
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}


/**
 * Hide number increments
 */

// hides the up and down arrows on number fields
input[type="number"] {
  -moz-appearance: textfield;
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
    width: auto;
  }
}

/**
 * Checkbox
 */
input[type='checkbox'] {
  content: "";
  position: relative;
  margin-top: 8px;
  margin-left: 0;

  &:after {
    content: "";
    vertical-align: middle;
    text-align: center;
    line-height: 17px;
    position: absolute;
    cursor: pointer;
    height: 17px;
    width: 17px;
    left: -1px;
    top: -4px;
    background: url($svgCheckbox);
    background-size: 17px;
  }
  &:checked:after {
    background: url($svgCheckboxChecked);
    background-size: 17px;
  }
  &.better:checked:after {
    background: url($svgRedCheckboxChecked);
    background-size: 17px;
  }
}

/**
 * Radiobox
 */
input[type='radio'] {
  content: "";
  position: relative;
  &:after {
    content: "";
    vertical-align: middle;
    position: absolute;
    cursor: pointer;
    height: 17px;
    width: 17px;
    left: -2px; top: -1px;
    background: url($svgRadiobox);
    background-repeat:no-repeat;
    background-size: 17px;
  }
  &:checked:after {
    left: -2px; top: -1px;
    background: url($svgRadioboxChecked);
    background-repeat:no-repeat;
    background-size: 17px;
  }
}

input[type='radio'].better {
  content: "";
  position: relative;
  &:after {
    content: "";
    vertical-align: middle;
    position: absolute;
    cursor: pointer;
    height: 17px;
    width: 17px;
    left: -2px; top: -1px;
    background: url($svgBetterRadioEmpty);
    background-repeat: no-repeat;
    background-size: 17px;
  }
  &:checked:after {
    left: -2px; top: -1px;
    background: url($svgBetterRadioChecked);
    background-repeat:no-repeat;
    background-size: 17px;
  }
}

/**
 * Slider
 */
input[type=range]{
  -webkit-appearance: none;
  background: transparent;
  height: 1px;
  @-moz-document url-prefix() {
    margin-left: 1px;
  }
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 16px;
  width: 16px;
  border-radius: 100%;
  background: $aqua;
  margin-top: -4px;
  box-shadow: 0 2px 2px;
}

input[type=range]::-moz-range-thumb {
  border: none;
  height: 16px;
  width: 16px;
  border-radius: 100%;
  background: $aqua;
  margin-top: -4px;
  box-shadow: 0 2px 2px;
}

input[type=range]::-moz-range-track {
  background-color: transparent;
}

.sliderContainer{
  display: flex;
  height: 5px;
  flex: 1;
  position: relative;
  border: solid 1px #E7E7E7;
  border-radius: 3px;
}

#sliderFill {
  height: 100%;
  background: $aqua;
  border-radius: 3px;

  &.better {
    background: $red;
  }
}