/**
 * Icons
 */
.lp-icon {
  opacity: 0.5;
  display: inline-block;
  stroke-width: 1.2px;
  background-size: cover;
  stroke: none;
  fill: $gray-shade-5;

  &:hover {
    opacity: 1;
    cursor: pointer;
  }
  &#searchNoHover:hover {
    opacity: .5;
    cursor: default;
  }
  &.search {
    background: url($svgIconSearch);
    background-repeat: no-repeat;
    width: 14px;
    height: 14px;
    margin-top: 5px;
    margin-right: 5px;
  }
  &.refresh {
    background: url($svgIconRefresh);
    background-repeat: no-repeat;
    width: 20px;
    height: 16px;
    margin-top: 4px;
  }
  &.questionMark {
    background: url($svgQuestionMark);
    background-repeat: no-repeat;
    width: 13px;
    height: 13px;
    vertical-align: middle;
  }
  &.close {
    background: url($svgIconClose);
    background-repeat: no-repeat;
    width: 14px;
    height: 14px;
    margin-top: 5px;
    margin-right: 5px;
  }
  &.carrot{
    background: url($svgIconCarrot);
    background-repeat: no-repeat;
    width: 10px;
    height: 5.84px;
  }
  &.down-arrow{
    background: url($svgIconDownArrow);
    width: 10px;
    height: 5.84px;
    margin-left: 2px;
    padding: 0px;
    position: relative;
    top: -1px;
  }
  &.iconMoreInfo {
    position: relative;
    display: inline-block;
    width: 10px;
    height: 10px;
    background: url($svgInfo);
    background-size: 10px 10px;
    background-repeat: no-repeat;
    opacity: 1;
    margin-left: 2px;
  }
  &.genPasswordIcon{
    background: url($svgGeneratePassword);
    background-repeat: no-repeat;
    width: 14px;
    height: 14px;
    margin-top: 5px;
  }
}

.lp-logo {
  content: url($svgLogo);
  width: 75px;
  vertical-align: center;
  position: relative;
  padding: 21px 0px 21px 0px;
}
