/*
 * This is generated from sass, don't change css directly or it will get overwritten.
*/

@import '../components/sso/newvault/styles/variables';
@import '../components/sso/newvault/styles/mixins';
@import '../components/sso/newvault/styles/grid';
@import '../components/sso/newvault/styles/arrow';
@import '../components/sso/newvault/styles/buttons';
@import '../components/sso/newvault/styles/svg';
@import '../components/sso/newvault/styles/icons';
@import '../components/sso/newvault/styles/form_fields';
@import '../components/sso/newvault/styles/tile';
@import '../components/sso/newvault/styles/search';
@import '../components/sso/newvault/styles/passwordMeter';

$width-default: 360px;

body {
  margin: 14px 4px 4px 4px;
  @include font-lg();
}

@include border-radius($border-radius-base);
.infield-dialog {
  width: $width-default;
  background-color: $gray-tint-4;
  @include shadow(0 2px 6px $gray-shade-5);
  @include font-sm-dark();
  font-weight: $weight-light;
  display: none;
  border-radius: 3px;
  padding: 0 5px;

  &.container {
    margin-left: 0;
  }

  .dropdown {
    font-weight: $weight-normal;
    width: 150px;
    z-index: 99;
    cursor: pointer;
    background-color: white;
    border-radius: 4px;
    @include shadow(0 2px 6px $gray-shade-5);
    position: absolute;
    ul {
      margin: 0;
      padding: 0 2px;
      list-style: none;
      z-index: 99;
      li {
        display: inherit;
        overflow: hidden;
        &:hover {
          color: $red;
        }
        p {
          margin: 0px;
          padding: 13px 9px;
        }
      }
    }
  }
  .header {
    height: 40px;
    padding: 10px 0;

    .trigger > * {
      font-weight: 600;
      font-size: 14px;
      cursor: pointer;
    }
    p {
      display:inline-block;
      margin: 0;
    }
    #navigationMenu {
      top: -13px;
      left: 0;
      font-size: 14px;
      font-weight: 600;
      @include shadow(0px 2px 3px $gray-tint-1);
      @include rounded(2px);
      min-width: 175px;
    }
  }

  .content {
    max-height: 280px;
    overflow: auto;

    .italic {
      font-style: italic;
    }

    #generateAndFillBtn {
      height: 50px;
    }

    #password {
      width: 280px;
      height: 50px;
      padding-left: 10px;
      font-size: 14px;
      font-family: "Open Sans", sans-serif;
      border: none;
      position: relative;
    }

    #generatedPasswordLength {
      margin: 0;
      color: $gray;
    }

    .first-col {
      border-right-width: 1px;
      border-right-style: solid;
      border-right-color: $gray-tint-45;
    }

    #length {
      width: 100%;
      position: absolute;
      left: 0;
    }

    .advanced-background {
      background-color: white;
      margin-top: -20px;
      padding: 15px 0;
    }

    #generateAndFillView {
      height: 50px;
    }

    #fillPasswordBtn {
      width: 80px;
      height: 50px;
      position: relative;
    }

    .lp-icon:hover .tooltiptext {
      visibility: visible;
    }
    .tooltiptext {
      visibility: hidden;
      width: 200px;
      background-color: white;
      color: $gray-shade-5;
      text-align: center;
      padding: 5px 0;
      position: absolute;
      z-index: 1;
      @include shadow(0 2px 6px $gray-shade-5);
      left: 72%;
      top: -100%;
    }

    .pad-horizontal {
      padding: 0 10px;
    }

    input[disabled] + label {
      color: gray;
    }

    input[disabled]:after {
      cursor: default;
    }

    .meter-gen-pass {
      div {
        border-bottom-left-radius: 2 * $border-radius-base;
      }
    }
  }
  .footer {
    padding: 9px 0;
    height: 40px;

    .logo-sm-margin {
      margin-top: 7px;
    }
    .col-4 {
      .options-btn {
        font-size:11px;
      }
    }
  }

  .gradient-to-right {
    position: relative;
    &:after {
      content: " ";
      width: 1.5rem;
      height: calc(100% - 5px);
      position: absolute;
      right: 0;
      top: 0;
      @include gradient3(right, rgba($white,0), rgba($white,1), rgba($white,1));
    }
  }

  #btnCopy{
    margin-right: 5px;
  }

  #copied{
    display: none;
    margin-right: 5px;
    color: #a1adb5;
    cursor: pointer;
  }
}

@supports (-ms-ime-align:auto) {
  .infield-dialog .footer .col-4 .options-btn {
    padding-left:5px;
    padding-right:5px;
  }
}
#btnClose {
  right: 13px;
}