$tileHeight: 70px;
$lineHeight: 40px;

#sitesView {
  padding: 0px 10px;
}

.tile {
  background-color: $white;
  @include box-sizing(border-box);
  @include shadow(0px 1px 4px rgba($gray-shade-5, .3)); //#BCCAD4
  @include rounded(4px);
  @include font-md();

  margin-bottom: 12px;

  &.lastTile {
    margin-bottom: 2px;
  }

  .row {
    .icon {
      display: table-cell;
      vertical-align: middle;
      text-align: center;
      height: $tileHeight;
      width: $tileHeight;

    }
    .text {
      padding: 18px 0 18px 0;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      line-height: $lineHeight / 2;
      width: calc(100% - 60px);

      &.full-height {
        padding-top: 25px;
        padding-bottom: 25px;
        .small {
          line-height: 20px;
        }
      }
      .small {
        @include font-xs();
        line-height: 16px;
      }
    }
    .more {
      opacity: 0;
      width: 0;
      display: none;

      button {
        @include border-corner-radius(tr, 2 * $border-radius-base);
        @include border-corner-radius(br, 2 * $border-radius-base);

        border: 0;
        height: $tileHeight;
        padding: 0px 20px;
        font-size: $font-size-xs;
        color: $gray-shade-5;
        background-color: $gray-tint-5;
        cursor: pointer;
        width: 80px;

        &:hover, &:focus, &.menuTriggered {
          background-color: $gray-tint-5;
          outline: none;
        }
      }
    }
  }
  .icon-default {
    display: inline;
    margin-left: 10px;
    margin-right: 10px;
    width: 40px;
    @include rounded(3px);

    &.icon-site {
      width: 32px;
      height: 32px;
    }
  }

  &:hover, &:focus, &.triggered, &.menuTriggered {
    cursor: pointer;
    outline: none;
    @include border-corner-radius(tr, 2 * $border-radius-base);
    @include border-corner-radius(br, 2 * $border-radius-base);
    @include shadow(0px 1px 4px rgba($gray-shade-5, .7));

    .row {
      .text {
        width: calc(100% - 140px);
      }
      .more {
        display: block;
        opacity: 1;
        width: 80px;
      }
    }
  }
}

.tile-sm {
  height: 60px;

  .row {
    .icon {
      height: 60px;
    }
    .text {
      &.full-height {
        padding-top: 20px;
        padding-bottom: 20px;
        padding-left: 8px;
      }
    }
  }
  .icon-default {
    display: block;
    width: 36px;
    height: 36px;
  }
}