
.meter-gen-pass {
  height: 5px;
  max-width: 355px;
  margin-top: -1px;
  position: relative;

  > div {
    height: 5px;
    background-color: #F3F4F3;
      &.score-segment {
        display: inline-block;
        border-left: 2px solid #F3F4F7; 
        background-color: #DDDFE2;
        height: 3px;
        vertical-align: top;
      }

    @each $scoreHeader, $score in (poor: $poor-meter, bad: $bad-meter, ok: $ok-meter, good: $good-meter, great: $great-meter, best: $best-meter) {
      &.#{$scoreHeader} {
        background-color: $score;
      }
    }
  }
}
